//    

body {
  font-family: $primaryFont;
  font-size: 16px;
  font-weight: 500;
  color: getColor('black');
  background-color: getColor('background');
  min-height: 100vh;

  @include desk {
    height: 100vh;
  }
}

.overflow-hidden body {
  overflow: hidden;

  @include desk {
    overflow: auto;
  }
}

//  

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

//    
a {
  color: inherit;
  text-decoration: none;
}

//    
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

//       
img {
  display: block;
  // max-width: 100%;
  width: 100%;
  height: auto;
}
